import { clsx, type ClassValue } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export function generateSlug(text: string, id?: string): string {
  // ÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÜÿµ ÿßŸÑÿπÿ±ÿ®Ÿä ÿ•ŸÑŸâ ÿ±ÿßÿ®ÿ∑ ŸÖŸÜÿßÿ≥ÿ®
  const arabicToEnglish: { [key: string]: string } = {
    'ÿ£': 'a', 'ÿ®': 'b', 'ÿ™': 't', 'ÿ´': 'th', 'ÿ¨': 'j', 'ÿ≠': 'h', 'ÿÆ': 'kh',
    'ÿØ': 'd', 'ÿ∞': 'dh', 'ÿ±': 'r', 'ÿ≤': 'z', 'ÿ≥': 's', 'ÿ¥': 'sh', 'ÿµ': 's',
    'ÿ∂': 'd', 'ÿ∑': 't', 'ÿ∏': 'z', 'ÿπ': 'a', 'ÿ∫': 'gh', 'ŸÅ': 'f', 'ŸÇ': 'q',
    'ŸÉ': 'k', 'ŸÑ': 'l', 'ŸÖ': 'm', 'ŸÜ': 'n', 'Ÿá': 'h', 'Ÿà': 'w', 'Ÿä': 'y',
    'ÿ©': 'h', 'Ÿâ': 'a', 'ÿ°': 'a'
  };

  let slug = text
    .toLowerCase()
    .trim()
    // ÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑÿ£ÿ±ŸÇÿßŸÖ ÿßŸÑÿπÿ±ÿ®Ÿäÿ© ÿ•ŸÑŸâ ÿ•ŸÜÿ¨ŸÑŸäÿ≤Ÿäÿ©
    .replace(/[Ÿ†-Ÿ©]/g, (d) => 'Ÿ†Ÿ°Ÿ¢Ÿ£Ÿ§Ÿ•Ÿ¶ŸßŸ®Ÿ©'.indexOf(d).toString())
    // ÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑÿ≠ÿ±ŸàŸÅ ÿßŸÑÿπÿ±ÿ®Ÿäÿ©
    .split('')
    .map(char => arabicToEnglish[char] || char)
    .join('')
    // ÿ•ÿ≤ÿßŸÑÿ© ÿßŸÑÿ±ŸÖŸàÿ≤ ŸàÿßŸÑŸÖÿ≥ÿßŸÅÿßÿ™
    .replace(/[^a-z0-9\s-]/g, '')
    .replace(/\s+/g, '-')
    .replace(/-+/g, '-')
    .replace(/^-|-$/g, '');

  // ÿ•ÿ∂ÿßŸÅÿ© ID ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸÖÿ™ŸàŸÅÿ±ÿßŸã
  if (id) {
    slug = `${slug}-${id.substring(0, 8)}`;
  }

  return slug || 'project';
}